
import store from "@/store";
import { ADD_LOCATION } from "@/store/mutationTypes";
import { defineComponent } from "vue";
import SearchResult from "@/components/SearchResult.vue";
import { Location, Response } from "@/types";

export default defineComponent({
  name: "SearchComponent",
  components: { SearchResult },
  emits: ["error"],
  data() {
    return {
      country: "",
      city: "",
      results: new Array<Location>(),
      timer: 0,
    };
  },
  methods: {
    addLocation(city: Location) {
      store.commit(ADD_LOCATION, {
        id: city.id,
        city: city.city,
        country: city.country,
        country_code: city.country_code,
      });
      this.city = "";
      this.results = [];
      this.$router.push(`city/${city.id}`);
    },
    async searchTimeOut() {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = 0;
      }
      this.timer = setTimeout(() => {
        this.searchCity();
      }, 500);
    },
    async searchCity() {
      try {
        const searchRequest = `https://nominatim.openstreetmap.org/search/${this.city}?format=json&addressdetails=1&limit=5&polygon_svg=1`;
        const response = await fetch(searchRequest);
        const data = await response.json();
        const results = data.map((item: Response) => {
          if (typeof item == "object" || item !== null) {
            return {
              id: item.place_id,
              city: item.address.city
                ? item.address.city
                : item.address.town
                ? item.address.town
                : item.address.state,
              country: item.address.country,
              country_code: item.address.country_code,
            };
          }
        });
        this.results = results;
      } catch (error: unknown) {
        if (error instanceof Error) {
          this.setError(error.message);
          console.log(error.message);
        }
      }
    },
    setError(errorText: string) {
      this.$emit("error", errorText);
    },
  },
});
